import { Routes, RouterModule } from '@angular/router';

export const routes: Routes = [
    { path: '', redirectTo: 'tv', pathMatch: 'full' },
    { path: 'tv', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\tv\\tv.module')['default']);  });}) },
    { path: 'coder', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\coder\\coder.module')['default']);  });}) } //lazy-loading
];

export const appRoutingProviders: any[] = [

];

export const routing = RouterModule.forRoot(routes);


